*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body,
main,
#actions,
footer {
  display: flex;
}

body,
main {
  flex-direction: column;
}

main,
#filters,
.spacer {
  flex-grow: 1;
}

main,
footer {
  padding: var(--padding-primary);
}

#actions,
footer {
  flex-direction: row;
}

#actions {
  margin-top: var(--margin-primary);
}

button {
  font-weight: 700;
  line-height: 34px;
  cursor: pointer;
}

#details {
  margin: auto;
}

button:not(#unselect) {
  padding: 4px 20px;
  border-radius: var(--border-radius);
}

#filters {
  padding: 4px var(--padding-secondary);
  border: 1px solid var(--border-color-secondary);
  border-radius: var(--border-radius);
  background-color: transparent;
  font-family: "Source Sans Pro", sans-serif;
  font-size: inherit;
  outline: none;
  resize: none;
}

#preview {
  margin: auto var(--margin-secondary);
  border: 1px solid var(--border-color-ternary);
  color: var(--color-link);
  background-color: var(--background-color-ternary);
}

#unselect {
  border: 0;
  background: transparent;
  color: var(--color-link);
}

#selected,
#cancel,
#filters {
  color: var(--color-dimmed);
}

#selected::before {
  display: inline-block;
  min-width: 24px;
  height: 24px;
  padding-left: 4px;
  padding-right: 4px;
  border-radius: 50%;
  line-height: 24px;
  text-align: center;
  color: var(--background-color-primary);
  background-color: var(--color-default);
  content: attr(data-count);
}

html:not([dir="rtl"]) #selected,
html:not([dir="rtl"]) #selected::before {
  margin-right: var(--margin-secondary);
}

html[dir="rtl"] #selected,
html[dir="rtl"] #selected::before {
  margin-left: var(--margin-secondary);
}

#block,
#block-reload {
  margin: auto;
  border: 1px solid var(--color-brand-primary);
  color: var(--color-brand-primary);
  background-color: #fae9e9;
}

#block:disabled,
#block-reload:disabled,
#filters:disabled,
#preview:disabled {
  opacity: 0.5;
  cursor: default;
}

footer {
  background-color: var(--background-color-secondary);
}

#cancel {
  justify-content: space-between;
  align-items: center;
  border: 1px solid var(--border-color-secondary);
  background-color: transparent;
}

#logo {
  margin: auto;
  height: 22px;
}

html:not([dir="rtl"]) #logo {
  margin-right: 0;
}

html[dir="rtl"] #logo {
  margin-left: 0;
}
